li,
a {
  font-weight: 500;
  font-size: 2rem;
  text-decoration: none;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1% 10%;
}

.nav_links li {
  display: inline-block;
  padding: 0% 20px;
}

.nav_links li a {
  transition: all 0.3s ease 0s;
  font-size: 2rem;
}

.nav_links li a:hover {
  color: #59b256;
}
