.about {
  height: 100vh;
  display: flex;
}

.about-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-right {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.about-right p {
  font-size: 1.5rem;
  font-weight: 200;
  width: 75%;
}

.about-title {
  font-size: 3rem;
  font-weight: bold;
  color: #59b256;
}

.about-description-top {
  margin-bottom: 2%;
}
